@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 400;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-italic.woff2") format("woff2"), url("../fonts/bloggersans-italic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 500;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-mediumitalic.woff2") format("woff2"), url("../fonts/bloggersans-mediumitalic.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-style: italic;
  font-weight: 700;
  font-family: "Blogger Sans";
  font-display: swap;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  margin: 0;
  flex-direction: column;
  min-width: 320px;
  background-color: #161c35;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    font-size: 28px;
    line-height: 24px;
    background-color: #182044;
  }
}

.page-form {
  color: #444444;
}
@media (min-width: 768px) {
  .page-form {
    background-color: #edeff6;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding: 0 5px;
    max-width: 1330px;
  }
}

.section-description {
  padding: 0 25px;
}
@media (min-width: 768px) {
  .section-description {
    padding-left: 34px;
  }
}
@media (min-width: 1440px) {
  .section-description {
    padding-left: 70px;
    padding-right: 0;
  }
}

.section-description__text {
  margin: 0;
  margin-bottom: 18px;
  font-weight: 300;
}
@media (min-width: 768px) {
  .section-description__text {
    line-height: 33px;
    margin-bottom: 31px;
  }
}
@media (min-width: 1440px) {
  .section-description__text {
    margin-bottom: 40px;
  }
}

.section-description__text--bold {
  margin-bottom: 0;
  font-weight: 700;
}
@media (min-width: 768px) {
  .section-description__text--bold {
    font-size: 21px;
    line-height: 25px;
    margin-bottom: 54px;
  }
}
@media (min-width: 1440px) {
  .section-description__text--bold {
    max-width: 470px;
    margin-bottom: 86px;
  }
}

.section-description__text br {
  display: none;
}
@media (min-width: 768px) {
  .section-description__text br {
    display: block;
  }
}
@media (min-width: 1440px) {
  .section-description__text br {
    display: none;
  }
}

.text-wrapper {
  width: 100%;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .text-wrapper {
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .text-wrapper {
    margin: 0 auto;
    padding: 0;
    max-width: 1292px;
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link-button {
  display: block;
  padding: 9px 40px;
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #161c35;
  background-color: #ffd74b;
  border-radius: 25px;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.link-button:hover {
  background-color: #ffbd30;
}
.link-button:active {
  color: rgba(29, 46, 91, 0.3);
}
.link-button:active::after {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .link-button {
    max-width: 300px;
    font-size: 24px;
    line-height: 24px;
    padding-left: 32px;
  }
}

.link-button--showall {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  max-width: 270px;
  margin: 0 auto;
  padding: 20px 21px 20px 23px;
  text-align: left;
  border-radius: 10px;
  text-transform: none;
  color: #1d2e5b;
}
.link-button--showall::after {
  display: block;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-right-width: 0;
  border-bottom: 6px solid transparent;
  border-left: 12px solid #1d2e5b;
  content: "";
}
@media (min-width: 768px) {
  .link-button--showall {
    max-width: none;
    margin: 0 35px;
    padding: 29px 36px 29px 32px;
    border-radius: 20px;
  }
  .link-button--showall::after {
    right: 33px;
  }
}
@media (min-width: 1440px) {
  .link-button--showall {
    max-width: 275px;
  }
}

.link-button--header {
  margin-bottom: 51px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-size: 17px;
}
@media (min-width: 768px) {
  .link-button--header {
    display: none;
  }
}

.link-button--auth {
  font-size: 17px;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.link-button--auth:hover {
  background-color: #ffbd30;
}
@media (min-width: 1440px) {
  .link-button--auth {
    padding: 16px 28px 14px 29px;
    font-size: 20px;
    line-height: 20px;
  }
}

@media (max-width: 1439px) {
  .link-button--desktop-show {
    display: none;
  }
}
@media (min-width: 768px) {
  .link-button--desktop-show {
    padding: 12px 28px 14px 29px;
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .link-button--desktop-show {
    display: none;
  }
}

.logo__icon {
  fill: #ffffff;
}
@media (min-width: 768px) {
  .logo__icon {
    position: absolute;
    z-index: 1;
    top: 12px;
    right: 0;
    width: 107px;
    height: 17px;
  }
}
@media (min-width: 1440px) {
  .logo__icon {
    top: 14px;
    right: -7px;
    width: 129px;
    height: 20px;
  }
}

.logo__link {
  position: relative;
}
@media (min-width: 768px) {
  .logo__link {
    display: block;
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .logo__link {
    display: block;
    width: 200px;
    height: 50px;
  }
}

.logo__icon--footer {
  fill: #000000;
}

.logo__icon--showtablet {
  position: static;
  width: 155px;
  height: 42px;
}
@media (max-width: 767px) {
  .logo__icon--showtablet {
    display: none;
  }
}
@media (min-width: 1440px) {
  .logo__icon--showtablet {
    width: 200px;
    height: 50px;
  }
}

@media (max-width: 767px) {
  .logo--visible-tablet {
    display: none;
  }
}

.tooltip {
  display: none;
  position: absolute;
  top: 40px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  z-index: 10;
}

.tooltip__text {
  position: relative;
  margin: 0;
  padding: 18px 21px;
  background-color: #101d41;
  border-radius: 4px;
}
.tooltip__text::after {
  position: absolute;
  top: -5px;
  left: calc(50% - 7px);
  width: 14px;
  height: 14px;
  background-color: #101d41;
  transform: rotate(45deg);
  content: "";
}

.user-list {
  display: flex;
  justify-content: center;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .user-list {
    display: none;
  }
}

.user-list--header {
  display: none;
}
@media (min-width: 1440px) {
  .user-list--header {
    display: flex;
    flex-wrap: wrap;
    max-width: 300px;
    margin-bottom: 0;
    margin-left: auto;
    z-index: 5;
  }
}
.user-list--header .user-list__link {
  width: 50px;
  height: 50px;
}

.user-list__link {
  position: relative;
  display: block;
  width: 40px;
  height: 40px;
  text-decoration: none;
  color: inherit;
  border-radius: 50%;
}

.user-list__link--tel {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ff8d30;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.user-list__link--tel::after {
  position: absolute;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-call.svg");
  content: "";
}
.user-list__link--tel:hover {
  background-color: #f26926;
}
.user-list__link--tel:active::after {
  opacity: 0.3;
}

.user-list__link--email {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffbd30;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.user-list__link--email::after {
  position: absolute;
  width: 18px;
  height: 16px;
  background-image: url("../img/icon-top-menu-mail.svg");
  content: "";
}
.user-list__link--email:hover {
  background-color: #f26926;
}
.user-list__link--email:active::after {
  opacity: 0.3;
}

.links-list {
  padding-top: 51px;
  padding-bottom: 54px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .links-list {
    text-align: left;
    padding: 73px 0 135px 57px;
  }
}
@media (min-width: 1440px) {
  .links-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    padding-left: 90px;
    max-width: 760px;
  }
  .links-list::before {
    position: absolute;
    top: calc(50% + 1px);
    left: 1px;
    width: 60px;
    height: 2px;
    background-color: #ffffff;
    content: "";
  }
}

.links-list__item {
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 1440px) {
  .links-list__item:hover {
    color: #ffd74b;
    transform: translateY(-147%);
  }
}

.links-list__link {
  position: relative;
  font-size: 30px;
  line-height: 35px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: #192144;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.links-list__link:hover {
  color: #ffd74b;
}
.links-list__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .links-list__link {
    font-size: 60px;
    line-height: 71px;
  }
}
@media (min-width: 1440px) {
  .links-list__link {
    padding: 21px 16.5px 12px 16.5px;
    font-size: 20px;
    line-height: 20px;
    color: #ffffff;
  }
  .links-list__link::before {
    top: 100%;
    position: absolute;
    content: attr(data-hover);
  }
  .links-list__link:hover {
    color: #ffd74b;
    transform: translateY(-200px);
  }
}

.top-menu__container {
  background-color: #ffffff;
  color: #ffffff;
  min-width: auto;
}
@media (min-width: 768px) {
  .top-menu__container {
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .top-menu__container {
    background-color: #161c35;
  }
}

.top-menu {
  min-height: 73px;
}
@media (min-width: 768px) {
  .top-menu {
    min-height: 132px;
  }
}
@media (min-width: 1440px) {
  .top-menu {
    background-color: #161c35;
  }
}

.top-menu__icon {
  fill: #161c35;
}
@media (min-width: 1440px) {
  .top-menu__icon {
    fill: #ffffff;
  }
}

@media (min-width: 1440px) {
  .top-menu__list {
    margin-left: -5px;
  }
}

.top-menu--open {
  background-color: #ffffff;
}
.top-menu--open .top-menu__container {
  background-color: #ffffff;
}
.top-menu--open .top-menu__nav {
  padding-top: 25px;
  padding-bottom: 17px;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
@media (min-width: 768px) {
  .top-menu--open .top-menu__nav {
    padding-top: 40px;
    padding-bottom: 24px;
  }
}
.top-menu--open .top-menu__wrapper {
  top: 64px;
  display: flex;
  position: absolute;
}
@media (min-width: 768px) {
  .top-menu--open .top-menu__wrapper {
    top: 107px;
  }
}
@media (min-width: 1440px) {
  .top-menu--open .top-menu__wrapper {
    position: static;
    padding: 0;
    width: auto;
    background-color: transparent;
  }
}
.top-menu--open .top-menu__icon {
  fill: #192144;
}
.top-menu--open .top-menu__toggle {
  display: block;
}
.top-menu--open .top-menu__toggle::after {
  top: 2px;
  transform: rotate(45deg);
  background-color: #cccccc;
}
.top-menu--open .top-menu__toggle::before {
  top: 2px;
  transform: rotate(-45deg);
  background-color: #cccccc;
}
@media (min-width: 1440px) {
  .top-menu--open .top-menu__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .top-menu--open {
    background-color: #161c35;
  }
  .top-menu--open .top-menu__container {
    background-color: transparent;
    padding: 0;
  }
  .top-menu--open .top-menu__icon {
    fill: #ffffff;
  }
}

@media (min-width: 1440px) {
  .top-menu--close {
    background-color: #161c35;
  }
}
.top-menu--close .top-menu__container {
  background-color: #161c35;
}
@media (min-width: 768px) {
  .top-menu--close .top-menu__container {
    background-color: #192144;
  }
}
@media (min-width: 1440px) {
  .top-menu--close .top-menu__container {
    background-color: #161c35;
  }
}
.top-menu--close .top-menu__icon {
  fill: #ffffff;
}
.top-menu--close .top-menu__nav {
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding-top: 18px;
  padding-bottom: 24px;
}
@media (min-width: 768px) {
  .top-menu--close .top-menu__nav {
    padding-top: 40px;
  }
}
@media (min-width: 1440px) {
  .top-menu--close .top-menu__nav {
    justify-content: flex-start;
    padding-top: 48px;
    padding-left: 0;
    padding-right: 0;
    background-color: #161c35;
  }
}
.top-menu--close .top-menu__wrapper {
  top: -2000px;
  display: flex;
  position: absolute;
}
@media (min-width: 1440px) {
  .top-menu--close .top-menu__wrapper {
    position: static;
    padding: 0;
    width: auto;
    background-color: transparent;
  }
}
.top-menu--close .top-menu__toggle {
  display: block;
}
.top-menu--close .top-menu__toggle::after {
  display: block;
  top: 0;
  left: -1px;
  width: 25px;
  height: 3px;
  content: "";
}
.top-menu--close .top-menu__toggle::before {
  display: block;
  top: 8px;
  left: -1px;
  width: 25px;
  height: 3px;
  content: "";
}
@media (min-width: 1440px) {
  .top-menu--close .top-menu__toggle {
    display: none;
  }
}

.top-menu--scroll .top-menu__container {
  position: fixed;
  z-index: 20;
  width: 100%;
  background-color: #ffffff;
}
.top-menu--scroll .top-menu__icon {
  fill: #192144;
}
.top-menu--scroll .top-menu__nav {
  padding-top: 24px;
  padding-bottom: 27px;
}
.top-menu--scroll .top-menu__toggle::after {
  background-color: #cccccc;
}
.top-menu--scroll .top-menu__toggle::before {
  background-color: #cccccc;
}
.top-menu--scroll .top-menu__links-list {
  padding-bottom: 37px;
}
@media (min-width: 768px) {
  .top-menu--scroll .top-menu__nav {
    padding-top: 40px;
    padding-bottom: 42px;
  }
}
@media (min-width: 1440px) {
  .top-menu--scroll .top-menu__container {
    background-color: #ffffff;
    max-width: none;
    width: 100%;
  }
  .top-menu--scroll .top-menu__links-list {
    position: static;
    padding-bottom: 0;
  }
  .top-menu--scroll .top-menu__nav {
    background-color: #ffffff;
    padding-top: 21px;
    padding-bottom: 19px;
    max-width: 1340px;
    margin: 0 auto;
  }
  .top-menu--scroll .top-menu__list::before {
    background-color: #000000;
  }
  .top-menu--scroll .top-menu__link {
    color: #000000;
  }
  .top-menu--scroll .top-menu__icon {
    fill: #161c35;
  }
}

.social-list {
  display: flex;
  justify-content: center;
  min-width: 135px;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .social-list {
