}
.social-list__link--footer:hover {
  opacity: 1;
}
.social-list__link--footer:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .social-list__link {
    width: 60px;
    height: 62px;
  }
  .social-list__link--footer {
    width: 62px;
    height: 62px;
    margin-bottom: 0;
  }
  .social-list__link--fb {
    background-size: 14px 26px;
  }
  .social-list__link--vk {
    background-size: 27px 18px;
  }
  .social-list__link--ig {
    background-size: 21px 21px;
  }
