.contacts-list {
  margin-bottom: 36px;
}
@media (min-width: 768px) {
  .contacts-list {
    padding-left: 62px;
    margin-bottom: 111px;
  }
}
@media (min-width: 1440px) {
  .contacts-list {
    padding-left: 0;
  }
}
@media (min-width: 1440px) {
  .contacts-list--header {
    display: none;
  }
}

.contacts-list__link {
  position: relative;
  display: block;
  padding-top: 12px;
  padding-bottom: 11px;
  padding-left: 61px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffd74b;
