.catalog-card__heart-counter::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 31px;
  height: 31px;
  background-color: #e2e5f2;
  border-radius: 50%;
  z-index: 1;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.catalog-card__heart-counter::after {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 13px;
  height: 11px;
  background: url("../img/icon-heart.svg") no-repeat;
  z-index: 2;
  opacity: 0.3;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.catalog-card__heart-counter:hover::after {
  opacity: 0.5;
}
.catalog-card__heart-counter:hover::before {
  background-color: #e2e5f2;
}
.catalog-card__heart-counter:active::after {
  opacity: 0.1;
}
