.countries-list__letter {
  @media (max-width: 1439px) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: block;
    margin-bottom: 32px;
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    color: $basic-blue-dark;
  }
}

.countries-list__item {
  display: none;
  padding: 17px 50px 15px 19px;
  font-size: 16px;
  font-weight: 400;
  color: $special-grey;
  background-color: $white-color;
  border: 1px solid $range-input-border-color;
  border-radius: 0 0 4px 4px;

  &--active {
    display: block;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding: 30px 5px 50px 26px;
    min-width: 206px;
    border: none;
  }

  @media (min-width: $desktop-width) {
    display: none;
    padding-right: 59px;

    &--active {
      display: block;
    }
  }
}

.countries-list__subitem:not(:last-child) {
  margin-bottom: 4px;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.countries-list--catalog {
  .countries-list__item {
    background-color: transparent;
    border: none;
    padding: 13px 50px 29px 0;
  }

  @media (min-width: $tablet-width) {
    padding-top: 2px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;

    .countries-list__item {
      display: block;
      padding: 0;
    }
  }
}

.countries-list__label {
  cursor: pointer;
  transition: $basic-animation;

  &:hover {
    color: $basic-blue-dark;
  }

  &:active {
    opacity: 0.3;
  }
}
